#movie-details {
    background-color: #040B0F;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border: 1px solid black;
    border-radius: 1rem;
    margin: 7rem 0;
}

/* DETAILS-TOP */

.details-top h3 {
    padding: 2rem;
    text-align: center;
    font-size: 3rem;
    color: #FFD369;
}

.details-top iframe {
    width: 100%;
    height: 80svh;
    padding: 1rem 4rem;
}


/* DETAILS-BOTTOM */

.details-bottom {
    display: flex;
    height: 50svh;
}

.bottom-left {
    width: 20%;
    padding: 2rem 2rem;
}

.bottom-left img {
    object-fit: contain;
    width: 100%;
    height: 100%;
    margin-left: 2rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.bottom-right {
    display: flex;
    flex-direction: column;
    width: 80%;
    color: #fff;
    padding: 4rem;
    padding-right: 15rem;
    position: relative;
}

.bottom-right h3 {
    font-size: 3.4rem;
    margin-bottom: 3rem;
}

.bottom-right span {
    font-size: 1.8rem;
}

.description, .movie-date, .movie-genre, .details-imdb-rating p {
    margin-bottom: 2rem;
}

.imdb-button {
    width: 16%;
    padding: 1.6rem 2.4rem;
    background-color: #FFD369;
    color: #050E14;
    border: none;
    position: absolute;
    bottom: 4rem;
    right: 15rem;
    transition: all ease .4s;
    font-size: 1.6rem;
}

.imdb-button:hover {
    opacity: .8;
}







