.loading {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loading .loader {
    width: 48px;
    height: 48px;
    border: 5px solid #FFD369;
    border-bottom-color: transparent;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
    text-align: center;
    margin: 3rem 0;

}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
} 